.move {
  @include page-grid;

  &__title {
  grid-column: 1 / -1;

  @include on-tablet {
    grid-column: 1 / 4;
  }

  @include on-desktop {
    grid-column: span 5;
  }

  }

  &__description {
    margin: 0;
   grid-column: 1 / -1;

  @include on-tablet {
    grid-column: 4 / 7;
  }

  @include on-desktop {
    grid-column: span 6;
  }

    color: #FFF;
    font-size: 23px;
    font-weight: 500;
    line-height: 140%; /* 32.2px */
  }
}
