.bikes {
  &__products {
    @include page-grid;

    display: grid;
    row-gap: 40px;
  }
  &__product {
    @include hover(transform, scale(1.02));

    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}
